<template>
  <view class="nav-bar" :style="headerStyle">
    <view class="commonHead_wrap" :style="{ height: menuHeight + 'px' }">
      <view class="commonHead_left">
        <view class="back" @click="handleBack" v-if="showBack">
          <uni-icons type="left" size="20" :color="backColor"></uni-icons>
        </view>
        <view class="title" :style="{ color: titleColor }" @click="handleBack">
          {{ title }}
        </view>
        <view class="right_box" v-if="searchTitle">
          <view
            class="search_box"
            :style="{
              borderRadius: menuHeight + 'px',
              backgroundColor: searchColor,
            }"
            @click="handleSearch"
          >
            <uni-icons
              custom-prefix="iconfont"
              type="iconfont-search"
              size="18"
              color="#A8ABB2"
            ></uni-icons>
            <text class="text">{{ searchTitle }}</text>
          </view>
        </view>
      </view>
      <view class="commonHead_center"></view>
    </view>
  </view>
</template>

<script>
export default {
  name: 'navBar',
  props: {
    title: {
      type: String,
      default: '',
    },
    showBack: {
      type: Boolean,
      default: false,
    },
    backColor: {
      type: String,
      default: '#323233',
    },
    titleColor: {
      type: String,
      default: '#fff',
    },
    moduleKey: {
      type: String,
      default: 'stadium',
    },
    searchTitle: {
      type: String,
      default: '',
    },
    searchColor: {
      type: String,
      default: '#F7F9FC',
    },
    navColor: {
      type: String,
      default: '#fff',
    },
    isFixed: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      navBarHeight: 0,
      menuRight: 0,
      menuWidth: 0,
      menuTop: 0,
      menuHeight: 0,
    }
  },
  computed: {
    headerStyle() {
      let obj = {
        height: `${this.navBarHeight}px`,
        paddingLeft: `${this.menuRight * 2}px`,
        paddingRight: `${this.menuRight + this.menuWidth}px`,
        paddingTop: `${this.menuTop}px`,
        backgroundColor: this.navColor,
      }
      if (this.isFixed) {
        obj['position'] = 'fixed'
      }
      return obj
    },
  },
  created() {
    const app = getApp()
    let { menuRight, menuTop, navBarHeight, menuHeight, menuWidth } = this.$store.app.menuInfo
    console.log(this.$store.app.menuInfo)
    this.menuHeight = menuHeight
    this.menuTop = menuTop
    this.navBarHeight = navBarHeight
    this.menuWidth = menuWidth
    this.menuRight = menuRight
  },
  methods: {
    handleSearch() {
      uni.navigateTo({
        url: '/pages/search/search?module=' + this.moduleKey,
      })
    },
    handleBack() {
      uni.navigateBack({
        fail(err) {
          console.log(err)
          uni.switchTab({
            url: '/pages/tabbar/home/index',
          })
        },
      })
    },
  },
}
</script>

